
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.edmRightEditor{
  position: fixed;
  right: -20px;
  top: 0;
  height: 100%;
  width: 400px;
  background-color: #efefef;
  -webkit-box-shadow: 0px 0px 8px 3px #c2c2c2;
          box-shadow: 0px 0px 8px 3px #c2c2c2;
  z-index: 10;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding: 40px 10px 0 10px;
  overflow: auto;
}
.closed-button{
  position: fixed;
  top: 0;
  height: 42px;
  right: 0;
  z-index: 15;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: #efefef;
  width: 380px;
  border-bottom: 1px solid #c2c2c2;
}
.historyBox{
  position: fixed;
  bottom: -200px;
  height: 242px;
  right: 0;
  z-index: 15;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: #efefef;
  width: 380px;
  border-top: 1px solid #c2c2c2;
}
.historyBox.historyOpen{
  -webkit-transform: translateY(-200px);
          transform: translateY(-200px);
}
.historyBox.closed{
  background-color: transparent;
  opacity: 0;
  border-bottom: none;
  -webkit-transform: translateX(400px);
          transform: translateX(400px);
}
.closed-button a{
  color: #1d1d1d;
  position: absolute;
  left: 100px;
  top: 10px;
  text-decoration: underline;
  font-size: 16px;
  cursor: pointer;
}
.closed-button .restoreCode{
  left: 20px;
}
.edmRightEditor.closed,.closed-button.closed{
  -webkit-transform: translateX(400px);
          transform: translateX(400px);
  opacity: 0;
}
.closed-button.closed{
  background-color: transparent;
  opacity: 1;
  border-bottom: none;
}
.closed-button.closed a {
  display: none;
}
.close{
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 41px;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.close span{
  width: 30px;
  height: 3px;
  background-color: #1d1d1d;
  display:block;
  position: absolute;
  border-radius: 3px;
  left: 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.close span:nth-child(1){
  top: 11px;
  -webkit-transform: rotate(45deg)translate(0,-3px);
          transform: rotate(45deg)translate(0,-3px);
  -webkit-transform-origin: top left;
          transform-origin: top left;
}
.close span:nth-child(2){
  top: 19px;
  opacity: 0;
}
.close span:nth-child(3){
  top: 27px;
  -webkit-transform: rotate(-45deg)translate(0,3px);
          transform: rotate(-45deg)translate(0,3px);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}
.closed .close span{
  -webkit-transform: none;
          transform: none;
  opacity: 1;
}
.closed .close{
  -webkit-transform: translateX(-400px);
          transform: translateX(-400px);
}
.addTr,.addTd{
  width: 200px;
  height: 40px;
  border: 2px dashed #1d1d1d;
  margin: 10px auto;
  position: relative;
  opacity: 0.8;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-indent: 40px;
  font-weight: bold;
  cursor: pointer;
}
.addTr{
  -webkit-transform: translateX(-15px);
          transform: translateX(-15px);
}
.addTd{
  margin-top: 10px;
  width: 160px;
  height: 30px;
  text-indent: 25px;
  font-size: 14px;
}
.addTr span,.addTd span{
  width: 18px;
  height: 4px;
  display: block;
  background-color: #1d1d1d;
  position: absolute;
  left: 60px;
  top: 50%;
  border-radius: 5px;
}
.addTr.generate{
  text-indent: 0;
}
.addTd span{
  left: 45px;
  width: 14px;
  height: 2px;
}
.addTr span:nth-child(1){
  margin-left: -9px;
  margin-top: -2px;
}
.addTd span:nth-child(1){
  margin-left: -7px;
  margin-top: -1px;
}
.addTr span:nth-child(2){
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin-left: -9px;
  margin-top: -2px;
}
.addTd span:nth-child(2){
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  margin-left: -7px;
  margin-top: -1px;
}
.rightFloor{
  margin: 10px 0 10px 30px;
  width: 290px;
  padding: 10px 10px 0 10px;
  border: 2px solid #1d1d1d;
  border-radius: 5px;
  position: relative;
}
.rightFloor .floorNum{
  position: absolute;
  right: -50px;
  width: 50px;
  font-size: 14px;
  top: 0;
}
.rightFloor .del{
  width: 22px;
  height: 22px;
  background-color: #d90000;
  border-radius: 50%;
  position: absolute;
  right: -35px;
  top: 50%;
  margin-top: -11px;
  cursor: pointer;
}
.rightFloor .del:before{
  content:"";
  width: 14px;
  height: 2px;
  background-color: #fff;
  display: block;
  top: 50%;
  left: 50%;
  position: absolute;
  margin: -1px 0 0 -7px;
}
.rightFloorItem{
  border-bottom :1px solid #000;
  margin-top: 5px;
}
.rightFloorItem:first-child{
  margin-top: 0;
}
.floorItem{
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 3px;
}
.floorItem:nth-child(2){
  margin-bottom: 5px;
}
.floorItem label{
  min-width: 44px;
  text-align: left;
  font-weight: bold;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.floorItem input{
  width: 100%;
  line-height: 24px;
  padding: 0 3px;
  outline: none;
}
.historyButton{
  position: absolute;
  top: 0;
  text-decoration: underline;
  left: 20px;
  cursor: pointer;
  width: 200px;
  height: 40px;
  line-height: 40px;
  text-align: left;
}
.saveHistory{
  position: absolute;
  right: 20px;
  top: 8px;
  text-decoration: underline;
  cursor: pointer;
}
.historyList{
  position: absolute;
  left: 20px;
  right: 20px;
  top: 42px;
  bottom: 10px;
  overflow: auto;
}
.historyItem{
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 5px;
}
.historyItem input{
  width: 100%;
  background:transparent;
  border: none;
  outline: none;
  font-size: 16px;
  line-height: 20px;
}
.historyItem label{
  cursor: pointer;
  min-width: 50px;
  line-height: 20px;
  font-size: 16px;
}
.historyItem span{
  width: 16px;
  height: 16px;
  background-color: #d90000;
  border-radius: 50%;
  cursor: pointer;
  display: inline-block;
  min-width: 16px;
  position: relative;
  top: 3px;
  margin-right: 5px;
}
.historyItem span:before{
  content:'';
  width: 10px;
  height: 2px;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -1px 0 0 -5px;
  background-color: #fff;
}
.chooseTemplate{
  position: absolute;
  background-color: #efefef;
  padding: 0 20px;
  height: 40px;
  top: -41px;
  left: 0;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 40px;
  border-top: 1px solid #c2c2c2;
}
.tem{
