  height: 30px;
  background: url(data:image/svg+xml;base64,) no-repeat;
  background-size: 90% auto;
  background-position: center center;
}
.switch-list{
  width: 280px;
  height: 100%;
  position: absolute;
  left: 40px;
  background-color: #efefef;
  text-align: left;
  display: none;
}
.switch-list.switchActive{
